.color-panel {
  position: relative;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 1.45rem;
}
.color-panel .content {
  position: absolute;
  text-align: center;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  padding: .15rem;
}
.color-panel .content .title {
  margin-bottom: .1rem;
  font-size: .2rem;
}
.color-panel .content .more {
  margin: auto;
  width: 1.31rem;
  height: .32rem;
  font-size: .15rem;
  line-height: .32rem;
}
.color-panel::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  content: '';
  border-style: solid;
  border-width: 15px;
  border-color: transparent transparent #fff transparent;
}



