.swipper-wrapper {
  position: relative;
}
.swipper-wrapper .swipper-item {
  text-align: center;
  width: 100%;
}
.swipper-wrapper .swipper-item .swipper-image {
  width: 100%;
  height: 2.2rem;
}
.swipper-wrapper .swiper-dots {
  position: absolute;
  width: 100%;
  bottom: .07rem;
  text-align: center;
}
.swipper-wrapper .swiper-dots .dot {
  display: inline-block;
  width: 15px;
  height: 3px;
  margin-right: 10px;
  background: #d6d7d8;
}
.swipper-wrapper .swiper-dots .dot.active {
  background: #b4282d;
}



