.yx-types {
  display: -webkit-box;
}
.yx-types .item {
  position: relative;
  line-height: .3rem;
  font-size: .15rem;
  padding: 0 .15rem;
  white-space: nowrap;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.yx-types .item.active {
  color: #b4282d;
}
.yx-types .item.active::after {
  content: " ";
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 70%;
  bottom: 0;
  height: 2px;
  background: #b4282d;
}



