.good-list {
  white-space: nowrap;
  background: #fff;
  padding-bottom: .1rem;
}
.good-list .item {
  display: inline-block;
  width: 1.56rem;
  padding-left: .15rem;
}
.good-list .item .content {
  padding: 0 .1rem;
}
.good-list .item .content .name {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: .15rem;
  margin-bottom: .1rem;
  color: #333;
}
.good-list .item .content .describe {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  margin-bottom: .1rem;
  font-size: .13rem;
  color: #7f7f7f;
}
.good-list .item .content .price {
  font-size: .15rem;
  color: #b4282d;
}
.good-list .item .img-wrapper {
  position: relative;
  width: 100%;
  padding-top: 100%;
  background: #f4f4f4;
  margin-bottom: .2rem;
}
.good-list .item .img-wrapper img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}



