.cart-icon .cart {
  position: relative;
}
.cart-icon .cart .goods-num {
  display: block;
  min-width: 1.2em;
  text-align: center;
  height: 1.2em;
  position: absolute;
  background: #b4282d;
  right: -0.6em;
  color: #fff;
  font-size: .12rem;
  top: -0.6em;
  padding: .02rem;
  border-radius: 50%;
}



